	module.exports = {"data":{"markdownRemark":{"html":"<p><a href='https://docs.python.org/3/library/constants.html' target='_blank' rel='nofollow'>Constants</a></p>\n<p>Three commonly used built-in constants:</p>\n<ul>\n<li><code>True</code>: The true value of the <em>bool</em> type.</li>\n<li><code>False</code>: The false value of the <em>bool</em> type.</li>\n<li><code>None</code> : The sole value of the type <em>NoneType</em>. None is frequently used to represent the absence of a value, as when default arguments are not passed to a function.</li>\n</ul>\n<p>Constants added by the site module\nThe site module (which is imported automatically during startup, except if the -S command-line option is given) adds several constants to the built-in namespace. They are useful for the interactive interpreter shell and should not be used in programs.</p>\n<p>quit(code=None)\nexit(code=None)\nObjects that when printed, print a message like “Use quit() or Ctrl-D (i.e. EOF) to exit”, and when called, raise SystemExit with the specified exit code.</p>\n<p>copyright\nlicense\ncredits\nObjects that when printed, print a message like “Type license() to see the full license text”, and when called, display the corresponding text in a pager-like fashion (one screen at a time).</p>","fields":{"slug":"/python/built-in-constants/"},"frontmatter":{"title":"Python Built in Constants"}}},"pathContext":{"slug":"/python/built-in-constants/"}}

